<template>
  <div class="app-container">
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span></span>
      <a href="./static/download/stock.xlsx" style="margin-left: 8px;display: inline-block;">
        <el-button type="primary"></el-button>
      </a>
      <el-upload style="margin-left: 8px;display: inline-block;"
                 class=""
                 action=""
                 :on-change="handleChange"
                 :show-file-list="false"
                 :file-list="fileListUpload"
                 :limit="limitUpload"
                 accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                 :auto-upload="false"
                 :disabled="updateLoading">
        <el-button  type="primary" :loading="updateLoading" :disabled="updateLoading">Excel</el-button>
      </el-upload>
      <div style="margin-left: 8px;display: inline-block;">
        <el-button type="primary" :loading="updateLoading" :disabled="updateLoading" @click="handleUpdateStockList()"></el-button>
      </div>
    </el-card>
    <div class="table-container">
      <el-table ref="productTable"
                :data="list"
                style="width: 100%"
                v-loading="listLoading"
                border>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">{{scope.row.firm}}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.stock}}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">{{scope.row.model}}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.unit}}</template>
        </el-table-column>
        <el-table-column label="" width="140" align="center">
          <template slot-scope="scope">{{scope.row.num}}</template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">{{scope.row.price}}</template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">{{getTotalPrice(scope.row.num, scope.row.price)}}</template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">{{scope.row.other}}</template>
        </el-table-column>
        <el-table-column label="" width="160" align="center">
          <template slot-scope="scope">

            <el-button
              size="mini"
              @click="handleUpdateStock(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDeleteStock(scope.$index, scope.row)">
            </el-button>

          </template>
        </el-table-column>
      </el-table>

      <el-dialog
        title=""
        :visible.sync="editInfo.dialogVisible"
        :top="'10vh'"
        width="60%">
        <el-form :model="editInfo.stock" :rules="rules" ref="stockInfoForm" label-width="15%" style="width: 90%;margin: auto"
                 size="small">
          <el-form-item label="" prop="firm">
            <el-input v-model="editInfo.stock.firm"></el-input>
          </el-form-item>
          <el-form-item label="" prop="stock">
            <el-input v-model="editInfo.stock.stock"></el-input>
          </el-form-item>
          <el-form-item label="" prop="model">
            <el-input v-model="editInfo.stock.model"></el-input>
          </el-form-item>
          <el-form-item label="" prop="unit">
            <el-input v-model="editInfo.stock.unit"></el-input>
          </el-form-item>
          <el-form-item label="" prop="num">
            <el-input-number v-model="editInfo.stock.num"></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="price">
            <el-input-number v-model="editInfo.stock.price"></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="other">
            <el-input v-model="editInfo.stock.other"></el-input>
          </el-form-item>
          <el-form-item style="text-align: center">
            <el-button type="primary" size="medium" @click="handleEditStock('stockInfoForm')"> </el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>
<script>
  import {updateStockList} from "@/api/stock";
  export default {
    name: 'addStock',
    data() {
      return {
        list: [],
        listLoading: false,
        editInfo: {
          index: null,
          dialogVisible: false,
          stock: {}
        },
        rules: {
          firm: [{required: true, message: '', trigger: 'blur'}],
          stock: [{required: true, message: '', trigger: 'blur'}],
          model: [{required: true, message: '', trigger: 'blur'}],
          unit: [{required: true, message: '', trigger: 'blur'}],
          num: [{required: true, message: '', trigger: 'blur'}],
          price: [{required: true, message: '', trigger: 'blur'}],
          other: [{required: false, message: '', trigger: 'blur'}]
        },
        fileTemp:null,
        fileListUpload: [],
        limitUpload: 2,
        updateLoading: false
      }
    },
    methods: {
      handleUpdateStock(index, row) {
        // 
        this.$nextTick(() => {
          if (this.$refs['stockInfoForm'] != null)
            this.$refs['stockInfoForm'].resetFields();
        })
        this.editInfo.index = index;
        this.editInfo.stock = Object.assign({}, row);
        this.editInfo.dialogVisible = true;
      },
      handleDeleteStock(index, row) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.list.splice(index,1);
        });
      },
      handleEditStock(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.list[this.editInfo.index]=Object.assign({}, this.editInfo.stock);
              this.list = [...this.list];
              this.$message({
                message: '',
                type: 'success',
                duration: 1000
              });
              this.editInfo.dialogVisible = false;
            });
          }
        });
      },
      handleUpdateStockList() {
        if (this.list.length == 0){
          this.$message({
            message: '',
            type: 'warn',
            duration: 1000
          });
          return
        }
        this.listLoading = true;
        this.updateLoading = true;
        setTimeout(()=>
        updateStockList(this.list).then(response => {
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          });
          this.list = [];
          this.listLoading = false;
          this.updateLoading = false;
        }),1000);
      },
      getTotalPrice(num, price){
        let total = num * price;
        if(total < 0)
          total = 0;
        return total;
      },
      handleChange(file, fileList) {
        // console.log(file,fileList)
        if (fileList.length > 1) {
          fileList.splice(0, 1);
        }
        this.listLoading = true;
        this.fileTemp = file.raw;
        let fileName = file.raw.name;
        let fileType = fileName.substring(fileName.lastIndexOf('.') + 1);
        // 
        if (this.fileTemp) {
          if ((fileType == 'xlsx') || (fileType == 'xls')) {
            this.importf(this.fileTemp)
            this.listLoading = false;
          } else {
            this.$message({
              type: 'warning',
              message: ''
            });
            this.listLoading = false;
          }
        } else {
          this.$message({
            type: 'warning',
            message: ''
          });
          this.listLoading = false;
        }
      },
      importf(obj) {
        let _this = this;
        this.file = event.currentTarget.files[0];
        var rABS = false; //
        var f = this.file;
        var reader = new FileReader();
        //if (!FileReader.prototype.readAsBinaryString) {
        FileReader.prototype.readAsBinaryString = function (f) {
          var binary = "";
          var rABS = false; //
          var pt = this;
          var wb; //
          var outdata;
          var reader = new FileReader();
          reader.onload = function (e) {
            var bytes = new Uint8Array(reader.result);
            var length = bytes.byteLength;
            for (var i = 0; i < length; i++) {
              binary += String.fromCharCode(bytes[i]);
            }
            var XLSX = require('xlsx');
            if (rABS) {
              wb = XLSX.read(btoa(fixdata(binary)), { //
                type: 'base64'
              });
            } else {
              wb = XLSX.read(binary, {
                type: 'binary'
              });
            }
            // outdata excel
            outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
            // excel 
            let arr = []
            outdata.map(v => {
              // let jsonString = JSON.stringify(v).replace(/\*/g, '').replace(/\s/ig,'');
              let jsonString = JSON.stringify(v).replace(/\//g, '').replace(/\s/ig,'');
              // debugger;
              // console.log(jsonString);
              v = JSON.parse(jsonString);
              let obj = {}
              //xxx
              obj.firm = v[""]
              obj.stock = v[""]
              obj.model = v[""]
              obj.unit = v[""]
              obj.num = v[""]
              obj.price = v[""]
              obj.other = v[""]
              arr.push(obj)
            })
            _this.list = [...arr];
          }
          reader.readAsArrayBuffer(f);
        }
        if (rABS) {
          reader.readAsArrayBuffer(f);
        } else {
          reader.readAsBinaryString(f);
        }
      }
    }
  }
</script>
<style>
</style>



